import { SubGameDef } from "../../module_basic/scripts/SubGameDef";
import { ResPlaySlots } from "../../module_basic/shared/protocols/public/slots/PtlPlaySlots";
import { autoPanel } from "./autoPanel";
import { betPanel } from "./betPanel";
import { dataChange } from "./dataChange";
import { dragonDetail } from "./dragonDetail";
import { dragonMain } from "./dragonMain";
import { dragonSlotCtrl } from "./dragonSlotCtrl";
import { dragonWheel } from "./dragonWheel";
import { lastmoneyPanel } from "./lastmoneyPanel";
import { payPanel } from "./payPanel";
import { rulePanel } from "./rulePanel";

class cDragon {
    public static _instance: cDragon | null = null;

    public static get instance(): cDragon {
        if (!this._instance) {
            this._instance = new cDragon();
        }
        return this._instance;
    }
    
    public BETNUM = dataChange.BETNUM;
    public LINES = dataChange.LINES;
    public BET = dataChange.BET;

    private _isNewUser: boolean = false;
    public get isNewUser(): boolean {
        this._isNewUser = false;
        return this._isNewUser;
    }

    private _money: number = 0;
    public get money(): number {
        return this._money;
    }
    public set money(m: number) {
        this._money = m;
    }

    private _exchangeRate: number = 0;
    public get exchangeRate(): number {
        this._exchangeRate = 1;
        return this._exchangeRate;
    }

    /** 0 1 */
    private _musicControl: number = null!;
    public get musicControl(): number {
        return this._musicControl;
    }
    public set musicControl(num: number) {
        this._musicControl = num;
        // AudioMgr.inst.musicControl = num;
    }

    public detail: dragonDetail | null = null;
    public node: dragonMain = null!;
    public get slotCtrl(): dragonSlotCtrl {
        return this.node.slotCtrl;
    }

    public wheelList: dragonWheel[] = [];
    public bet: number = 0;
    public betNum: number = 0;
    public sumIdx: number = 0;

    /** */
    private _betSum: number = 0;

    public get betSum(): number {
        if (!this._betSum) {
            this._betSum = Number(localStorage.getItem(SubGameDef.SLOTS_DRAGON + "betSum"));
        }
        return this._betSum;

    }

    public set betSum(v: number) {
        if (this._betSum != v) {
            localStorage.setItem(SubGameDef.SLOTS_DRAGON + "betSum", v + "");
        }
        this._betSum = v;
    }

    /** */
    public lotteryRes: any = null!;

    /** */
    public auto: boolean = false;
    /** */
    public autoTimes: number = 0;
    /**012 */
    public status: number = 0;
    /** */
    public winLinePos: number[] = [];
    /** */
    public bigWinNum: number = 0;
    /** */
    public rollIndex: number = 0;
    public rollEnable: boolean = true;


    /** */
    public betPanel: betPanel = null!;
    /** */
    public autoPanel: autoPanel = null!;
    /** */
    public lastmoneyPanel: lastmoneyPanel = null!;


    constructor() {

    }
}

export let dragon = cDragon.instance;